
<template>
  <div>
    <baidu-map
    class="bm-view navigation-box" 
    @ready="handler"
    :center="center"
    :zoom="zoom"
    :style="'width:'+ deviceWidth+'px;height:'+deviceHeight+'px;'">
      <bm-marker :position="center" :dragging="true" animation="BMAP_ANIMATION_BOUNCE">
        <bm-label content="景德镇市第一人民医院" :labelStyle="{color: 'red', fontSize : '12px'}" :offset="{width: -35, height: 30}"/>
         <bm-scale anchor="BMAP_ANCHOR_BOTTOM_RIGHT"></bm-scale>
          <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_RIGHT"></bm-navigation>
      </bm-marker>
    </baidu-map>
  </div>
</template>


<script>

  export default {
    name: '',
    data(){
      return{
        deviceWidth:'',
        deviceHeight:'',
        center: {lng:0, lat: 0},
        zoom: 3
      }
    },
    mounted(){
      this.geDevice()
    },
    methods:{
      handler ({BMap, map}) {
        console.log(BMap, map)
        this.center.lng = 117.210257
        this.center.lat =  29.311085
        this.zoom = 17
      },
      geDevice(){
        this.deviceWidth = window.screen.width
        this.deviceHeight = window.screen.height
      },
    },
  };
</script>
<style lang='scss' scoped>
.navigation-box{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
</style>


